raul-slider {
  display: block;
  font-size: 0.875rem;
}

raul-slider .r-form-element input[type=number] {
  -moz-appearance: textfield !important;
  /*For FireFox*/
}

raul-slider .r-form-element input[type=number]::-webkit-inner-spin-button {
  /*For Webkits like Chrome and Safari*/
  -webkit-appearance: none;
  margin: 0;
}

raul-slider .r-form-element__control {
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  height: 1.25rem;
  line-height: 1.25;
  flex-grow: 1;
  position: relative;
}

raul-slider .r-form-element__control__track {
  background-color: #C6CCD0;
  display: flex;
  align-items: center;
  flex-grow: 1;
  height: 0.25rem;
  border-radius: 9999px;
  overflow: hidden;
  position: relative;
}

raul-slider .r-form-element__control__track__progress {
  height: 0.25rem;
}

raul-slider .r-form-element__control__track__progress--transparent {
  background-color: transparent;
}

raul-slider .r-form-element__control__track__progress--primary {
  background-color: #0076CC;
}

raul-slider .r-form-element__control__track__progress--warning {
  background-color: #FEC12D;
}

raul-slider .r-form-element__control__track__progress--error {
  background-color: #D01A1F;
}

raul-slider .r-form-element__control__track__progress--success {
  background-color: #139C3E;
}

raul-slider .r-form-element__control__track__progress--disabled {
  background-color: #9BA3A7;
}

raul-slider .r-form-element__control--range input[type=range] {
  pointer-events: none;
}

raul-slider .r-form-element__control__slider {
  display: block;
  cursor: pointer;
  height: 1.25rem;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: transparent;
  -webkit-appearance: none;
}

raul-slider .r-form-element__control__slider::-webkit-slider-thumb {
  height: 1.25rem;
  width: 1.25rem;
  border-width: 2px;
  background-color: #fff;
  border-radius: 9999px;
  border-style: solid;
  -webkit-appearance: none;
  pointer-events: auto !important;
}

raul-slider .r-form-element__control__slider::-moz-range-thumb {
  height: 1.25rem;
  width: 1.25rem;
  border-width: 2px;
  background-color: #fff;
  border-radius: 9999px;
  border-style: solid;
  pointer-events: auto !important;
}

raul-slider .r-form-element__control__slider::-ms-thumb {
  height: 1.25rem;
  width: 1.25rem;
  border-width: 2px;
  background-color: #fff;
  border-radius: 9999px;
  border-style: solid;
  pointer-events: auto !important;
}

raul-slider .r-form-element__control__slider--primary::-webkit-slider-thumb {
  border-color: #0076CC;
}

raul-slider .r-form-element__control__slider--primary::-moz-range-thumb {
  border-color: #0076CC;
}

raul-slider .r-form-element__control__slider--primary::-ms-thumb {
  border-color: #0076CC;
}

raul-slider .r-form-element__control__slider--error::-webkit-slider-thumb {
  border-color: #D01A1F;
}

raul-slider .r-form-element__control__slider--error::-moz-range-thumb {
  border-color: #D01A1F;
}

raul-slider .r-form-element__control__slider--error::-ms-thumb {
  border-color: #D01A1F;
}

raul-slider .r-form-element__control__slider--warning::-webkit-slider-thumb {
  border-color: #FEC12D;
}

raul-slider .r-form-element__control__slider--warning::-moz-range-thumb {
  border-color: #FEC12D;
}

raul-slider .r-form-element__control__slider--warning::-ms-thumb {
  border-color: #FEC12D;
}

raul-slider .r-form-element__control__slider--success::-webkit-slider-thumb {
  border-color: #139C3E;
}

raul-slider .r-form-element__control__slider--success::-moz-range-thumb {
  border-color: #139C3E;
}

raul-slider .r-form-element__control__slider--success::-ms-thumb {
  border-color: #139C3E;
}

raul-slider .r-form-element__control__slider:disabled {
  cursor: default !important;
}

raul-slider .r-form-element__control__slider:disabled::-webkit-slider-thumb {
  border-color: #9BA3A7;
  cursor: not-allowed;
}

raul-slider .r-form-element__control__slider:disabled::-moz-range-thumb {
  border-color: #9BA3A7;
  cursor: not-allowed;
}

raul-slider .r-form-element__control__slider:disabled::-ms-thumb {
  border-color: #9BA3A7;
  cursor: not-allowed;
}

raul-slider .r-form-element__control__slider:hover + .r-form-element__control__wrapper .r-form-element__control__tooltip {
  opacity: 1;
}

raul-slider .r-form-element__control__wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  padding: 0px 10px;
}

raul-slider .r-form-element__control__wrapper__formatter {
  position: relative;
  flex-grow: 1;
}

raul-slider .r-form-element__control__tooltip {
  position: absolute;
  background-color: #37474F;
  color: #fff;
  font-size: 0.75rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  border-radius: 0.125rem;
  box-shadow: 0 16px 27px 0 rgba(82, 97, 115, 0.22);
  border-radius: 0.125rem;
  z-index: 50;
  opacity: 0;
  top: -30px;
  left: 0%;
  transform: translateX(-50%);
  transition: opacity 0.1s ease;
}

raul-slider .r-form-element__control__tooltip__pointer {
  position: absolute;
  background-color: #37474F;
  color: #fff;
  font-size: 0.75rem;
  width: 0.5rem;
  height: 0.5rem;
  box-shadow: 0 16px 27px 0 rgba(82, 97, 115, 0.22);
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
}

raul-slider .r-form-element__label {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 0.75rem;
  font-weight: 500;
  margin-bottom: 0.75rem;
}

raul-slider .r-form-element__input {
  height: 1.5rem;
  position: relative;
}

raul-slider .r-form-element__input input[type=number] {
  border-color: #C6CCD0;
  border-width: 1px;
  height: 1.5rem;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: transparent;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  border-radius: 0.125rem;
  -webkit-appearance: none;
}

raul-slider .r-form-element__input input[type=number]:focus {
  outline: 0;
  border-color: #0076CC;
}

raul-slider .r-form-element__input--range {
  flex: 1 1 0%;
}

raul-slider .r-form-element__input--single {
  width: 50%;
}

raul-slider .r-form-element__input__dash {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.25rem;
  width: 1.25rem;
  height: 1.5rem;
}

raul-slider .r-form-element__input__symbol {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

raul-slider .r-form-element__input__symbol-append {
  left: auto;
  right: 0.75rem !important;
}

raul-slider .r-form-element__input__symbol-prepend {
  right: auto;
  left: 0.75rem !important;
}

raul-slider .r-form-element__scale {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 0.5rem;
  font-size: 0.75rem;
}

raul-slider .r-form-element--symbol-prepend input[type=number] {
  padding-left: 1.75rem !important;
  text-align: right;
}

raul-slider .r-form-element--symbol-prepend input[type=number]::-webkit-input-placeholder {
  text-align: right;
}

raul-slider .r-form-element--symbol-prepend input[type=number]::-moz-placeholder {
  text-align: right;
}

raul-slider .r-form-element--symbol-prepend input[type=number]:-ms-input-placeholder {
  text-align: right;
}

raul-slider .r-form-element--symbol-prepend input[type=number]::-ms-input-placeholder {
  text-align: right;
}

raul-slider .r-form-element--symbol-prepend input[type=number]::placeholder {
  text-align: right;
}

raul-slider .r-form-element--symbol-prepend input[type=number]::-webkit-input-placeholder {
  text-align: right;
}

raul-slider .r-form-element--symbol-append input[type=number] {
  padding-right: 1.75rem !important;
}

raul-slider .r-form-element--disabled .r-form-element__input {
  color: #9BA3A7;
}

raul-slider .r-form-element--disabled input[type=number] {
  background-color: #EBEDEE;
  border-color: #EBEDEE;
}

raul-slider .r-form-element--compact:not(.r-form-element--search) input[type=range]::-webkit-input-placeholder {
  color: transparent;
}

raul-slider .r-form-element--compact:not(.r-form-element--search) input[type=range]::-moz-placeholder {
  color: transparent;
}

raul-slider .r-form-element--compact:not(.r-form-element--search) input[type=range]:-ms-input-placeholder {
  color: transparent;
}

raul-slider .r-form-element--compact:not(.r-form-element--search) input[type=range]::-ms-input-placeholder {
  color: transparent;
}

raul-slider .r-form-element--compact:not(.r-form-element--search) input[type=range]::placeholder {
  color: transparent;
}

raul-slider .r-form-element--focused input[type=range]::-webkit-input-placeholder {
  color: #9BA3A7 !important;
}

raul-slider .r-form-element--focused input[type=range]::-moz-placeholder {
  color: #9BA3A7 !important;
}

raul-slider .r-form-element--focused input[type=range]:-ms-input-placeholder {
  color: #9BA3A7 !important;
}

raul-slider .r-form-element--focused input[type=range]::-ms-input-placeholder {
  color: #9BA3A7 !important;
}

raul-slider .r-form-element--focused input[type=range]::placeholder {
  color: #9BA3A7 !important;
}